<template>
  <div id="header" >
      <vue-position-sticky :offsetTop="0" >
        <transition name="navbar">
          <v-col v-show="showNav" class="showNav">
              <Toolbar></Toolbar>
          </v-col>
        </transition>
      </vue-position-sticky>
    <v-row class="header_row" lg="3" md="3" sm="12">
        <router-link to="/home">
            <img src="../assets/Logo7636.jpg" height="120" />
        </router-link>
        <v-col class="ma-auto">
          <div cols="12" lg="9" md="9" sm="12" class="display-2">
            <strong>FRC #7636 Robomania</strong>
          </div>
            <v-col class="pt-0">
              <Toolbar></Toolbar>
            </v-col>
        </v-col>
    </v-row>
  </div>
</template>

<!--
<v-col class="showNav" :class="{'showNav':scrollNav}">
  <Toolbar></Toolbar>
</v-col>
-->
<script>
import Toolbar from "./ToolBar.vue";

export default {
  components: {
    Toolbar,
  },
  data() {
        return{
            showNav: null,
        };
      },
    mounted(){
          window.addEventListener('scroll', this.updateSroll);
        },
    methods:{
      updateSroll(){
        if( window.scrollY > 60){
            this.showNav = true;
            return;
        }else{
          this.showNav = false;
          return
        }
      },
    }
};
</script>

<style scoped lang="scss">
@import "@/assets/scss/header.scss";

strong{
  font-family: 'Assistant';
  font-style: italic;
}
</style>
